import React from 'react'
import { Table, Icon, Input, Button } from 'antd'
import { connect } from 'react-redux'
import { Helmet } from 'react-helmet'
import _ from 'lodash'
// import table from './data.json'
import styles from './style.module.scss'

const mapStateToProps = ({ promotion }) => ({
  promotionData: promotion.promotionData,
})

const mapDispatchToProps = dispatch => ({
  getPromotionData: () => dispatch({ type : 'promotion/GET_DATA' }),
});


class Promotion extends React.Component {
  
  
  state = {
    tableData: null,
    data: null,
    filterDropdownVisible: false,
    searchText: '',
    filtered: false,
  }

  async componentDidMount() {
    const {getPromotionData} = await this.props;
    getPromotionData()
  }

  async componentWillReceiveProps(nextProps){

    // const { promotionData } = await this.props;
    // const dataPromotionNotChange = _.isEqual(nextProps.promotionData, promotionData);  

 
    if (!_.isEmpty ( nextProps.promotionData ) ) {
          this.setState({
            'tableData': nextProps.promotionData,
            'data': nextProps.promotionData
        })
    }
  }

  onInputChange = e => {
    this.setState({ searchText: e.target.value })
  }

  onSearch = () => {
    const { searchText, tableData } = this.state
    const reg = new RegExp(searchText, 'gi')
    this.setState({
      filterDropdownVisible: false,
      filtered: !!searchText,
      data: tableData
        .map(record => {
          const match = record.merchantName.match(reg)
          if (!match) {
            return null
          }
          return {
            ...record,
            name: (
              <span>
                {record.merchantName
                  .split(reg)
                  .map((text, i) =>
                    i > 0 ? [<span className="highlight">{match[0]}</span>, text] : text,
                  )}
              </span>
            ),
          }
        })
        .filter(record => !!record),
    })
  }

  linkSearchInput = node => {
    this.searchInput = node
  }

 
 

  routeChange = (path) => {
    const { history } = this.props
    history.push(path)
  }


  render() {
    
    const { data, searchText, filtered, filterDropdownVisible } = this.state
    const columns = [
      {
        title: 'ID',
        dataIndex: 'merchantID',
        key: 'merchantID',
        render: text => (
          <a className="utils__link--underlined" href="javascript: void(0);">
            {`#${text.substr(0, 4)}`}
          </a>
        ),
        sorter: (a, b) => a.id - b.id,
      },
      {
        title: 'Name',
        dataIndex: 'merchantName',
        key: 'merchantName',
        sorter: (a, b) => a.merchantName.length - b.merchantName.length,
        render: text => (
          <a className="utils__link--underlined" href="javascript: void(0);">
            {text}
          </a>
        ),
        filterDropdown: (
          <div className="custom-filter-dropdown">
            <Input
              ref={this.linkSearchInput}
              placeholder="Search name"
              value={searchText}
              onChange={this.onInputChange}
              onPressEnter={this.onSearch}
            />
            <Button type="primary" onClick={this.onSearch}>
              Search
            </Button>
          </div>
        ),
        filterIcon: <Icon type="search" style={{ color: filtered ? '#108ee9' : '#aaa' }} />,
        filterDropdownVisible,
        onFilterDropdownVisibleChange: visible => {
          this.setState(
            {
              filterDropdownVisible: visible,
            },
            () => this.searchInput && this.searchInput.focus(),
          )
        },
      },
      {
        title: 'Discount',
        dataIndex: 'discount',
        key: 'discount',
        sorter: (a, b) => a.discount - b.discount,
      },
      {
        title: 'Thumbnail',
        dataIndex: 'imgContent',
        key: 'imgContent',
        render: text => (
          <a href="javascript: void(0);" className={styles.thumbnail}>
            <img src={text} alt="" />
          </a>
        ),
      },
      {
        title: 'Body Content',
        dataIndex: 'bodyContent',
        key: 'bodyContent',
        sorter: (a, b) => a.bodyContent.length - b.bodyContent.length,
      },
      {
        title: 'Price',
        dataIndex: 'price',
        key: 'price',
        render: text => <span>{`$${text}`}</span>,
        sorter: (a, b) => a.price - b.price,
      },
      {
        title: 'Type',
        dataIndex: 'categories',
        key: 'categories',
        sorter: (a, b) => a.categories.length - b.categories.length,
      },
      // {
      //   title: 'Status',
      //   dataIndex: 'status',
      //   key: 'status',
      //   sorter: (a, b) => a.status.length - b.status.length,
      //   render: record => <span className="font-size-12 badge badge-success">{record}</span>,
      // },
      {
        title: 'Action',
        key: 'action',
        render: () => (
          <span>
            <Button icon="edit" className="mr-1" size="small">
              View
            </Button>
            <Button icon="cross" size="small">
              Remove
            </Button>
          </span>
        ),
      },
    ]

    return (
      <div>
        <Helmet title="Promotions List" />
        <div className="card">
          <div className="card-header">
            <div className="utils__title">
              <strong>Promotion List</strong>
            </div>
            <Button icon="edit" className="mr-2" size="small" onClick={() => this.routeChange('/promotion/create')}>
              Add
            </Button>
          </div>
          <div className="card-body">
            <Table
              className="utils__scrollTable"
              scroll={{ x: '100%' }}
              columns={columns}
              dataSource={data}
            />
          </div>
        </div>
      </div>
    )
  }
}

export default connect( mapStateToProps , mapDispatchToProps )(Promotion)
